$tekBlue: #1cb5d8;
$deepBlue: #006281;
$tekGreen: #73BF44;
$white: #fff;
$grey: #f5f5f5;
$mid-grey: #d7d7d7;

.ic-landing {
  
  &#root {
    position: relative;
    font: normal normal 18px/1.2 "Microsoft YaHei", sans-serif;
  }
  
  .clear-float::after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }

  .main-section {
    padding: 30px 20%;
  }

  .section-title {
    position: relative;
    width: 200px;
    height: 75px;
    line-height: 70px;
    margin: 30px auto 50px;
    text-align: center;
    color: $tekBlue;
    font-size: 2.2em;
    &::after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 100px;
      height: 5px;
      transform: translateX(-50%);
      background-image: linear-gradient(to right, $tekBlue, $tekGreen);
    }

  }

  .section-caption {
    width: 60%;
    margin: 0 auto;
    padding: 1.5rem;
    transform: translateY(-40%);
    background-color: $white;
    border-radius: 10px;
    font-size: 1rem;
    color: #333;
    text-align: center;
  }

  .btn {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    appearance: none;
    background-color: $tekBlue;
    border: 1px solid transparent;
    border-radius: 1.25rem;
    color: $white;
    cursor: pointer;
    display: inline-block;
    font-size: 0.75rem;
    -webkit-font-smoothing: antialiased;
    line-height: 1.2;
    margin: .6rem 0;
    padding: 0.3rem 1.25rem;
    text-decoration: none;
    text-transform: uppercase;
    transition: background-color 150ms ease;
    user-select: none;
    vertical-align: middle;
    white-space: nowrap;
    outline: none;
    word-spacing: 0.25rem;
  }

  .btn-primary {
    background-color: $tekBlue;
    &:hover {
      background-color: $deepBlue;
    }
  }

  .btn[disabled] {
    background-color: $mid-grey;
  }

  .popup-mask {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(120, 120, 120, .6);
    z-index: 98;
    display: none;
  }

  .popup-window {
    position: fixed;
    left: 50%;
    top: 50%;
    width: 460px;
    min-height: 300px;
    padding: .8rem 1.2rem;
    background-color: $white;
    border-top: 3px solid $tekBlue;
    transform: translate(-50%, -50%);
    z-index: 99;
    display: none;
    h3 {
      margin-top: 0;
      font-size: 1rem;
    }
    .mktoButtonWrap {
      margin-left: 0 !important;
    }
  }

  .popup-window-close {
    position: absolute;
    right: .6rem;
    top: .6rem;
    width: 20px;
    height: 20px;
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/close-button.png") no-repeat center;
    background-size: cover;
    cursor: pointer;

  }

  @keyframes wandering{
    0%{left: 0px;top: 50%}
    25%{left: 5px;top: 51%}
    50%{left: 10px;top: 50%}
    75%{left: 5px;top: 49%}
    100%{left: 0px;top: 50%}
  }

  .more-info-section-lg{
    position: fixed;
    left: 0;
    top: 50%;
    width: 120px;
    height: 338px;
    background:url("https://www.tek.com.cn/sites/default/files/2018-01/Side-banner.png") no-repeat top;
    background-size: cover;
    transform: translateY(-50%);
    z-index: 97;
    text-decoration: none;
    animation: wandering 5s infinite;
  }

  header {
    position: relative;
    .header-background {
      width: 100%;
      height: 500px;
      background: url("https://www.tek.com.cn/sites/default/files/2018-01/Top-banner.jpg") no-repeat center fixed;
      background-size: cover;
      filter: blur(1px);
    }

  }

  .header-caption {
    position: absolute;
    left: 50%;
    top: 40%;
    width: 60%;
    transform: translate(-50%, -50%);
    text-align: center;
    h1, h2 {
      color: $white;
      text-shadow: 1px 1px 2px #333;
    }
    h1 {
      font-size: 4rem;
    }
    h2 {
      font-size: 3.2rem;
    }
  }

  .data-section {
    position: relative;
    padding-top: 0;
    padding-bottom: 10px;
    background-color: $mid-grey;
    .section-caption {
      width: 75%;
      p {
        &:first-child {
          margin-top: 0;
        }
      }
      .data-section-logo {
        width: 60%;
        margin: 30px auto 0;
        text-align: center;
        display: flex;
        div {
          flex: 1;
          img {
            width: 100%;
          }
          &:first-child {
            margin-right: 2rem;
          }
          &:last-child {
            img {
              width: 80%;
              margin: 0 auto;
            }
          }
        }
      }
    }
    .data-section-data {
      margin-top: -80px;
      ul {
        display: flex;
        padding: 0;
        li {
          flex: 1;
          text-align: center;
          list-style: none;
          padding: .5rem;
          &:not(:last-child) {
            margin-right: 1rem;
          }
          h3 {
            height: 60px;
            span {
              font-size: 24px;
            }
          }
          h2 {
            margin-top: .4em;
            margin-bottom: 1rem;
            font-size: 3rem;
          }
        }
      }
    }
    .sprite {
      background-image: url(https://www.tek.com.cn/sites/default/files/2018-01/spritesheet.png);
      background-repeat: no-repeat;
      display: inline-block;
      margin-right: 10px;
    }

    .sprite-article {
      width: 32px;
      height: 32px;
      background-position: 0 0;
    }

    .sprite-company {
      width: 32px;
      height: 32px;
      background-position: -32px 0;
    }

    .sprite-handout {
      width: 32px;
      height: 32px;
      background-position: -64px 0;
    }

    .sprite-people {
      width: 32px;
      height: 32px;
      background-position: -96px 0;
    }

  }

  .lesson-section-lg,
  .lesson-section-md,
  .lesson-section-sm {
    position: relative;
    background-color: #2e2d31;
  }

  .lesson-section-fix {
    position: absolute;
    right: 0;
    bottom: -70px;
    z-index: 1;
  }

  .lesson-item-list {
    position: relative;
    display: flex;
    padding: 0;
    z-index: 2;
  }

  .lesson-item {
    position: relative;
    flex: 1;
    margin-bottom: 20px;
    list-style: none;
    transition: all 1s;
    cursor: pointer;
    color: $white;

    &:not(:last-child) {
      margin-right: 1rem;
    }
    &::before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 10px;
      width: 30%;
      height: 3px;
      transform: translateX(-50%);
      transition: all .8s;
      background-image: linear-gradient(to right, $tekBlue, $tekGreen);
      opacity: 1;
    }
    &:hover {
      .lesson-brief {
        opacity: 0;
      }
      .lesson-detail {
        opacity: 1;
      }
      &::before {
        bottom: 20px;
        opacity: 0;
      }
    }
    h3 {
      text-align: center;
    }
  }

  .lesson-number {
    margin: 20px 0;
    text-align: center;
    font-size: 3rem;
  }

  .lesson-sm-detail {
    padding: .5rem 1rem 2rem;
    text-align: center;
    h2 {
      font-size: 1.2rem;
      margin: .5rem 0;
    }
    h3 {
      font-size: 1rem;
      margin: .5rem 0;
    }
  }

  .lesson-wrapper {
    position: relative;
    min-height: 260px;
    background-color: rgba(28, 181, 216, .8);
    border-radius: 15px;
    box-shadow: 0 0 2px 2px $deepBlue;

  }

  .lesson-brief {
    position: absolute;
    left: 0;
    top: 0;
    padding: .5rem 1rem 2rem;
    transition: opacity .8s;
    opacity: 1;
    h2 {
      position: relative;
      font-size: 1.4rem;
      margin-top: .6rem;
      margin-bottom: 2rem;
      &::after {
        content: '';
        position: absolute;
        width: 70px;
        height: 4px;
        left: 0;
        bottom: -1rem;
        background-color: $white;
      }
    }
    h3 {
      font-size: 1rem;
      text-align: left;
    }
  }

  .lesson-detail {
    position: absolute;
    left: 0;
    top: 0;
    padding: .5rem 1rem 2rem;
    transition: opacity .8s;
    opacity: 0;
    text-align: center;
    h2 {
      font-size: 1rem;
      margin: .5rem 0;
    }
    h3 {
      font-size: .8rem;
      margin: .5rem 0;
    }
  }

  .lesson-detail-image {
    display: block;
    width: 80px;
    height: 80px;
    margin: 10px auto;
    border-radius: 50%;
    box-shadow: 0 0 1px 2px $tekBlue;
  }

  .lesson-detail-image-1 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Mike.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-detail-image-2 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Ji.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-detail-image-3 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Zhao.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-detail-image-4 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Wang.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-detail-image-5 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Cai.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-detail-image-6 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Gao.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-detail-image-7 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Han.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-detail-image-8 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Teacher-Liu.jpg") no-repeat center;
    background-size: 80px 110px;
  }

  .lesson-section-sm {
    .lesson-item-list {
      flex-wrap: wrap;
    }
    .lesson-item {
      flex: 0 1 100%;
      margin-right: 0;
    }
  }

  .banner-section {
    width: 100%;
    height: 600px;
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/Content-banner.jpg") no-repeat center fixed;
    background-size: cover;
  }

  .seminar-section {
    background-color: $deepBlue;
    padding-top: 0;
    padding-bottom: 30px;
    .section-title {
      color: $white;
      margin-top: -10px;
      margin-bottom: 50px;
    }
    .section-caption {
      background-color: $tekBlue;
      color: $white;
      h2 {
        margin: 1rem 0;
      }
    }
  }

  .seminar-item-list {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin-bottom: 30px;
    margin-top: -20px;
  }

  .seminar-item {
    position: relative;
    flex: 1;
    list-style: none;
    color: $white;
    text-align: center;
    cursor: pointer;
    &:not(:last-child) {
      margin-right: 1rem;
    }
  }

  .seminar-brief {
    h2 {
      font-size: 1.8rem;
    }
    h3 {
      font-size: 1.6rem;
      margin-bottom: 0;
    }
  }

  .seminar-brief-image-1 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/City-Nanjing.jpg") no-repeat center;
  }

  .seminar-brief-image-2 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/City-Hefei.jpg") no-repeat center;
  }

  .seminar-brief-image-3 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/City-Shanghai.jpg") no-repeat center;
  }

  .seminar-brief-image-4 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/City-Xiamen.jpg") no-repeat center;
  }

  .seminar-brief-image-5 {
    background: url("https://www.tek.com.cn/sites/default/files/2018-01/City-Shenzhen.jpg") no-repeat center;
  }

  .seminar-brief-image {
    display: block;
    height: 200px;
    margin: 20px auto;
    border-radius: 20px;
    box-shadow: 0 0 1px 2px $tekBlue;
    background-size: cover;

  }

  .more-info-section {
    background-color: $mid-grey;
  }

  .more-info-section-item {
    position: relative;
    width: calc(50% - 2rem);
    padding-right: 2rem;
    p {
      padding-right: 2rem;
    }
    &::after {
      content: '';
      position: absolute;
      right: -2.5px;
      top: 50%;
      transform: translateY(-50%);
      width: 5px;
      height: 100px;
      background-color: #333;

    }
  }

  @media screen and (min-width: 1400px) {
    .lesson-section-lg {
      display: block;
    }
    .lesson-section-md {
      display: none;
    }
    .lesson-section-sm {
      display: none;
    }
  }

  @media screen and (min-width: 1001px) {
    .seminar-item {
      padding: 1rem 1rem;
    }
  }

  @media screen and (min-width: 769px) and (max-width: 1000px) {
    .seminar-item {
      width: 80%;
      flex-basis: 80%;
      margin-right: 0 !important;
    }
  }

  @media screen and (min-width: 769px) and (max-width: 1399px) {
    .lesson-section-lg {
      display: none;
    }
    .lesson-section-md {
      display: block;
    }
    .lesson-section-sm {
      display: none;
    }
    .header-caption {
      h1 {
        font-size: 3.2rem;
      }
      h2 {
        font-size: 2rem;
      }
    }

    .seminar-brief {
      padding-bottom: 0;
    }
    .seminar-brief-image {
      width: 100%;
    }
    .more-info-section {
      .section-title {
        margin-bottom: 40px;
      }
    }
    .more-info-section-item {
      width: 100%;
      padding-right: 0;
      img {
        width: 100% !important;
      }
      p {
        padding-right: 0;
      }
      &::after {
        display: none;
      }
    }
  }
  @media screen and (min-width: 769px) {
    .more-info-section{
      display: none;
    }
    .more-info-section-lg{
      display: block;
    }
  }

  @media screen and (max-width: 768px) {
    .lesson-section-lg {
      display: none;
    }
    .lesson-section-md {
      display: none;
    }
    .lesson-section-sm {
      display: block;
    }
    .more-info-section-lg{
      display: none;
    }
    .main-section {
      padding: 30px 10%;
    }
    .section-caption {
      width: 80% !important;
    }
    .header-caption {
      h1 {
        font-size: 3rem;
      }
      h2 {
        font-size: 2rem;
      }
    }
    .data-section {
      .section-caption {
        transform: translateY(-20%);
      }
    }
    .data-section-logo {
      width: 100% !important;
      display: block !important;
      div {
        margin: 0 auto 15px !important;
        width: 90%;
        &:last-child {
          width: 70%;
          margin-bottom: 0 !important;
        }
      }
    }
    .data-section-data {
      ul {
        display: block !important;
      }
      li {
        display: list-item;
        width: calc(50% - 1.5rem);
        float: left;
        margin-right: .5rem !important;

        h2 {
          font-size: 2.6rem !important;
        }
      }
    }
    .banner-section {
      background-position: -400px center;
    }
    .seminar-section {
      .section-caption {
        h2 {
          font-size: 1.3rem;
        }
      }
    }
    .seminar-item {
      width: 100%;
      flex-basis: 100%;
      margin-right: 0 !important;
    }
    .seminar-brief {
      padding-bottom: 0;
    }
    .seminar-brief-image {
      width: 100%;
    }
    .more-info-section {
      .section-title {
        margin-bottom: 40px;
      }
      .more-info-section-button{
        text-align: center;
        a{
          font-size: 1.2rem;
        }
      }
    }
    .more-info-section-item {
      width: 100%;
      padding-right: 0;
      img {
        width: 100% !important;
      }
      p {
        padding-right: 0;
      }
      &::after {
        display: none;
      }
    }
    .popup-window {
      width: 100%;
      height: 100%;
      padding: 0;
      h3 {
        margin: 1rem .6rem;
      }

    }
  }
}